.header {
  height: 80px;
}
.header-wrap {
  height: 100%;
  padding: 0 100px;
}
.header-wrap .nav {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-wrap .nav .logo > a {
  font-size: 32px;
  font-weight: bold;
  color: purple;
  line-height: 80px;
}
.header-wrap .nav > ul {
  display: flex;
  gap: 24px;
}
.header .aside {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 300px;
  z-index: 9997;
  background-color: #fff;
}
.header .aside-top {
  display: flex;
  justify-content: space-between;
  padding: 0 12px;
}
.header .aside-top .logo > a {
  font-size: 32px;
  font-weight: bold;
  color: purple;
  line-height: 80px;
}
.header .aside-mid > ul > li > a {
  display: inline-block;
  width: 100%;
  padding: 24px 12px;
}

.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  height: 80px;
}
.footer-wrap {
  height: 100%;
}
.footer-wrap > ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}
.footer-wrap > ul > li.action > a {
  color: blue;
}
.footer-wrap > ul > li > a {
  font-weight: 700;
  font-size: 20px;
}

button {
  padding: 10px;
  font-size: 16px;
}
button.close::before {
  display: inline-block;
  margin-right: 10px;
  content: "X";
}
button.heart::before {
  display: inline-block;
  margin-right: 10px;
  content: "♥";
}

div {
  white-space: pre-line;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.scss"],"names":[],"mappings":"AAAA,gBAAgB;AAAhB;EACE,YAAA;AAEF;AADE;EACE,YAAA;EACA,gBAAA;AAGJ;AAFI;EACE,YAAA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;AAIN;AAFQ;EACE,eAAA;EACA,iBAAA;EACA,aAAA;EACA,iBAAA;AAIV;AADM;EACE,aAAA;EACA,SAAA;AAGR;AAME;EACE,eAAA;EACA,MAAA;EACA,QAAA;EACA,aAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;AAJJ;AAKI;EACE,aAAA;EACA,8BAAA;EACA,eAAA;AAHN;AAKQ;EACE,eAAA;EACA,iBAAA;EACA,aAAA;EACA,iBAAA;AAHV;AAUU;EACE,qBAAA;EACA,WAAA;EACA,kBAAA;AARZ;;AAiBA;EACE,kBAAA;EACA,WAAA;EACA,SAAA;EACA,YAAA;AAdF;AAeE;EACE,YAAA;AAbJ;AAcI;EACE,aAAA;EACA,6BAAA;EACA,mBAAA;EACA,YAAA;AAZN;AAeU;EACE,WAAA;AAbZ;AAgBQ;EACE,gBAAA;EACA,eAAA;AAdV;;AAqBA;EACE,aAAA;EACA,eAAA;AAlBF;AAoBI;EACE,qBAAA;EACA,kBAAA;EACA,YAAA;AAlBN;AAsBI;EACE,qBAAA;EACA,kBAAA;EACA,YAAA;AApBN;;AAwBA;EACE,qBAAA;AArBF","sourcesContent":["@charset \"UTF-8\";\n.header {\n  height: 80px;\n}\n.header-wrap {\n  height: 100%;\n  padding: 0 100px;\n}\n.header-wrap .nav {\n  height: 100%;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n.header-wrap .nav .logo > a {\n  font-size: 32px;\n  font-weight: bold;\n  color: purple;\n  line-height: 80px;\n}\n.header-wrap .nav > ul {\n  display: flex;\n  gap: 24px;\n}\n.header .aside {\n  position: fixed;\n  top: 0;\n  right: 0;\n  height: 100vh;\n  width: 300px;\n  z-index: 9997;\n  background-color: #fff;\n}\n.header .aside-top {\n  display: flex;\n  justify-content: space-between;\n  padding: 0 12px;\n}\n.header .aside-top .logo > a {\n  font-size: 32px;\n  font-weight: bold;\n  color: purple;\n  line-height: 80px;\n}\n.header .aside-mid > ul > li > a {\n  display: inline-block;\n  width: 100%;\n  padding: 24px 12px;\n}\n\n.footer {\n  position: absolute;\n  width: 100%;\n  bottom: 0;\n  height: 80px;\n}\n.footer-wrap {\n  height: 100%;\n}\n.footer-wrap > ul {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  height: 100%;\n}\n.footer-wrap > ul > li.action > a {\n  col